
// Heading splash
// ============
// Heading with a splash to the right or left

.heading-splash {
    position: relative;
    
    .splash-heading {
        @extend %col-md-4;
        @extend %col-md-offset-1;
        background: $clr-light;
        text-align: center;
        position: relative;
        top: -70px;
        transform: skewY(-8deg);

        h2 {
            display: inline-block;
            position: relative;
            z-index: $z-m;
        }
        h2, h2 span {
            @extend %heading-xxxl;
            text-align: right;
        }
        h2 .first, h2 .last {
            display: block;
        }
        h2 .first {
            letter-spacing: 4px;
        }
        &:before {
            content: "";
            position: absolute;
            width: 294px;
            height: 100%;
            @extend %bg-lined;
        }
    }
    &:before {
        content: "";
        display: block;
        position: absolute;
        top: -390px;
        right: -630px;
        z-index: $z-m;
        width: 1073px;
        height: 1298px;
        background: url(../img/shape-splat.svg) no-repeat;
        transform: rotate(-35deg);
    }
    &.right {
        @include flexbox();

        .inner {
            flex: 1;
            @include order(2);
        }
        &:before {
            flex: 1;
            position: static;
            @include order(1);
        }
    }
}