
// Intro no description
// ============
// Intro without any description text

.intro-no-description {

    .inner {
        background: $clr-light;
        min-height: 100vh;
        
        &:before {
            content: "";
            display: block;
            position: absolute;
            top: 120px;
            left: 0;
            z-index: $z-m;
            width: 50%;
            height: 450px;
            background: url(../img/bg-ovals.svg) no-repeat bottom right;
        }
        .splash-heading {
            
            &:before {
                content: "";
                display: block;
                position: absolute;
                top: -190px;
                left: -310px;
                z-index: $z-m;
                width: 1073px;
                height: 1298px;
                background: url(../img/shape-splat.svg) no-repeat;
            }
            h1, h1 span {
                font-family: $font-body;
                font-size: $font-l;
                font-weight: 100;
                letter-spacing: 15px;
                color: #fff;
                font-size: $font-m;
                text-transform: uppercase;
            }
            h1 {
                position: relative;
                top: 100px;
                left: 30px;
                z-index: $z-m + 1;
            }
            h1 span {
                font-size: $font-xl;
                font-weight: 500;
                letter-spacing: 30px;
                display: block;
            }
        }
        .lined-heading {
            @extend %bg-lined;
            width: 380px;
            position: absolute;
            top: $size-l;
            left: calc(50% + 100px);
            padding: 250px 0;

            h2 {
                transform: skewY(8deg);
                position: relative;
            }
            h2, h2 span {
                @extend %heading-xxxl;
            }
            h2 span {
                display: block;
                text-align: right;
                padding-right: 10px;
            }
        }
    }
}