// Patterns
// ============
// Global patterns and mixins go in here

%border-thin {
    border-bottom: 1px solid #ddd;
    padding-bottom: $size-s;
}
%bg-lined {
    background: url(../img/bg-lined.svg);
}
%bg-geometry {
    content: "";
    display: block;
    position: absolute;
    top: -70px;
    left: -10%;
    transform: rotate(-8deg);
    background: url(../img/bg-geometry.png) left;
    background-size: 100%;
    border-top: $size-m $clr-hlight solid;
    border-bottom: $size-m $clr-hlight solid;
    width: 120%;
    height: 1000px;
}
%pseudo {
    position: relative;

    &:before {
        content: "";
        display: block;
        background-size: cover;
        background-repeat: no-repeat;
        position: absolute;
    }
}